@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,800;1,400;1,500;1,100&display=swap');
*{
    margin: 0;
    padding: 0;
}
body{
    background: black;
    color: rgb(255, 255, 255);
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}
.wrapper{
    width: 80%;
    margin: 50px auto;
}
header .logo{
    font-weight: 800;
    font-size: 20;
}
header .presentation{
    margin: 50px 0;
    background: url(../static/images/regnum_picture_15957813341518345_normal.jpg) no-repeat;
    background-size: cover;
    background-position: center center;
    width: 100%;
    height: 500px;
    background-blend-mode: multiply;
    background-color: rgb(158, 255, 241);
    position: relative;
}
header .presentation::after{
    content: 'Добавляйте свои заметки с легкостью';
    position: absolute;
    margin: 50px 50px;
    font-weight: 800;
    font-size: 30px;
    width: 300px;
}
header .presentation::before{
    content: 'онлайн!';
    position: absolute;
    margin: 150px 50px;
    font-weight: 100;
    font-size: 15px;
    width: 200px;
}
footer{
    text-align: center;
    margin-top: 100px;
}


.nav{
    float: right;
    list-style: none;
    cursor: pointer;
    user-select: none;
}
.nav li{
    display: inline;
    margin-left: 25px;
    transition: color 500ms ease;
}
.nav  :hover{
    color: aqua;
}